*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#footer_section{
    height: 50vh;
    background-color: rgb(0,1,41);
    padding-top: 13%;
  
}

#footer_heading{
    color: red;
    width: 112%;
}

#footer_quick_links{
  
    width: 17%;
    height: 40vh;
    margin: 0 0 0 12%;
}

#contact_link{
    width: 12%;
}

.footer_links{
    transition: all 0.3s ease-in-out;
}

.footer_links:hover{
    margin-left: 3%;
    color: yellowgreen;
}

#me_footer{
    background-color:rgb(0,1,41) ;
    color: white;
}

@media screen and (max-width: 500px) {

    #footer_section{
        display: none;
    }

}